import React from 'react'
import { Link } from 'gatsby'

class Header extends React.Component {
  render () {
    const { title } = this.props

    return (
      <header className='py-5'>
        <h1 className='text-center'>
          <Link to={`/`} className='text-dark'>
            {title}
          </Link>
        </h1>
      </header>
    )
  }
}

export default Header
